import React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import './index.css';
import Header from './Header/Header.js';
import Home from './Home/Home.js';
import Minted from './Minted/Minted.js';
import My from './My/My.js';
import Attributes from './Attributes/Attributes.js';
import Faq from './Faq/Faq.js';
import Footer from './Footer/Footer.js';

const App = () => {

	return (
		<div className='app'>
			<Router>
				<Header />
					<Switch>
						<Route path='/' exact component={() => <Home />} />
						<Route path='/MintedCryptoFruits' exact component={() => <Minted />} />
						<Route path='/MyCryptoFruits' exact component={() => <My />} />
						<Route path='/Attributes' exact component={() => <Attributes />} />
						<Route path='/FAQ' exact component={() => <Faq />} />
					</Switch>
			  <Footer />
			</Router>
	  </div>
	);
};

ReactDOM.render(<App />, document.getElementById('root'));
