import React from 'react';
import { Link } from 'react-router-dom';

const Navbar = () => {

  const navItemClick = (classname) => {
  const items = document.getElementsByClassName('nav-item');
    for(let i = 0; i < items.length; i++){
      if((items[i].classList.contains('nav-item-active') && !items[i].classList.contains(classname)) || (!items[i].classList.contains('nav-item-active') && items[i].classList.contains(classname))){
        items[i].classList.toggle('nav-item-active');
      }
    }

    if(document.getElementsByClassName('nav')[0].classList.contains('open')){
      document.getElementsByClassName('mobile-menu')[0].classList.toggle('open');
      document.getElementsByClassName('nav')[0].classList.toggle('open');
    }

  }

  return (
    <div className="nav">
      <Link className="nav-item home" to='/' onClick={() => navItemClick("home")}>
        <p className="nav-item-placeholder">Home</p>
        Home
        <p className="nav-item-placeholder">Home</p>
      </Link>
      <Link className="nav-item minted" to='/MintedCryptoFruits' onClick={() => navItemClick("minted")}>
        <p className="nav-item-placeholder">Minted Crypto Fruits</p>
        Minted Crypto Fruits
        <p className="nav-item-placeholder">Minted Crypto Fruits</p>
      </Link>
      <Link className="nav-item my" to='/MyCryptoFruits' onClick={() => navItemClick("my")}>
        <p className="nav-item-placeholder">My Crypto Fruits</p>
        My Crypto Fruits
        <p className="nav-item-placeholder">My Crypto Fruits</p>
      </Link>
      <Link className="nav-item attributes" to='/Attributes' onClick={() => navItemClick("attributes")}>
        <p className="nav-item-placeholder">Attributes</p>
        Attributes
        <p className="nav-item-placeholder">Attributes</p>
      </Link>
      <Link className="nav-item faq" to='/FAQ' onClick={() => navItemClick("faq")}>
        <p className="nav-item-placeholder">FAQ</p>
        FAQ
        <p className="nav-item-placeholder">FAQ</p>
      </Link>
      <div className="nav-item nav-item-links">
        <div className="div-nav-item-links">
          <a className="links-img-div links-img-div-twitter" href="https://twitter.com/CryptoFruitsCFC?s=09" target="_blank" rel="noopener noreferrer">
            <img src={require('../images/twitterLogo.png')} alt="Twitter" className="twitter-img" />
          </a>
	  <a className="links-img-div links-img-div-discord" href="#discord" target="_blank" rel="noopener noreferrer">
            <img src={require('../images/discordLogo.png')} alt="Discord" className="discord-img" />
          </a>
        </div>
      </div>
    </div>
  );
};

export default Navbar;
