import React from 'react';

const MobileMenu = () => {

  const mobileMenuClick = () => {
    document.getElementsByClassName('mobile-menu')[0].classList.toggle('open');
    document.getElementsByClassName('nav')[0].classList.toggle('open');
  }

  return (
    <div className="mobile-menu" onClick={() => mobileMenuClick()}>
      <span></span>
      <span></span>
      <span></span>
      <span></span>
    </div>
  );
};

export default MobileMenu;
